	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.docuverse.com/blog/donpark/EntryViewPage.aspx?guid=a3e5a1ba-b79c-4cd1-8c39-64d9270e8b56\" title=\"Don Park's Daily Habit - Why\">Why Don Park blogs:</a></p>\n<blockquote><p>I don't blog to be recognized, get credit for being first of whatever, nor to amuse anyone. I am too much of a bastard for that. This blog is here to satisfy my own needs and no one else's. I tend to give a shit too much so I am trying hard to be selfish in the sense 'I don't give a hoot if other folks in this elevator called Earth suffocates if I fart right here and right now.'</p></blockquote>\n<p>Makes sense to me.</p>","fields":{"title":"Why I Blog","date":"14 May 2006","url":"https://papascott.de/2006-05-14-why-i-blog/"}}},"pathContext":{"slug":"/2006-05-14-why-i-blog/","prev":{"fields":{"title":"Baseball in Hamburg","date":"13 May 2006","slug":"/2006-05-13-baseball-in-hamburg/"}},"next":{"fields":{"title":"World Cup","date":"16 May 2006","slug":"/2006-05-16-world-cup/"}}}}

